export default {
	data(){
		return{
			city:['选择地址','',''],
			bossarea:'',
			avatar:'',
			upAvatar:false,
			License:'',
			uplicense:false,
			submitUrl:this.$store.state.baseurl+'/user/insertUser'
		}
	},
	onReady() {
		this.avatar=uni.getStorageSync('headpic')
		let _this=this;
		// 判断用户是否注册,已注册用户类型
		let openid=uni.getStorageSync('openid')
		if(openid==null||openid==undefined||openid==''){
			
		}else{
			uni.request({
				url:_this.$store.state.baseurl+'/user/verifyRole',
				data:{
					openId:openid
				},
				method:"POST",
				header : {'content-type':'application/x-www-form-urlencoded'},
				success:function(res){
					let type=res.data.data;
					//console.log(type)
					if(type==-1){
						
					}else if(type==-2){
						// uni.reLaunch({
						// 	url:'/pages/login/index'
						// })
					}else if(type==0){
						uni.reLaunch({
							url:'/pages/status/index'
						})
					}else if(type==2){
						uni.setStorageSync('userType',2)
						uni.reLaunch({
							url:'/pages/index/index'
						})
					}else if(type==3){
						uni.setStorageSync('userType',3)
						uni.reLaunch({
							url:'/pages/index01/index'
						})
					}
				}
			})
		}
	},
	onLoad(option) {
		//console.log('index onload')
		//this.$store.dispatch('get_data');
		uni.loadFontFace ({
		  family: 'Alibaba PuHuiTi',
		  source: 'url("https://www.mastervan.cn/static/project/dolphin/static/font/alifont/AlibabaPuHuiTi-Light.ttf")',
		  success: function(){
				console.log('load font success')
			  },
			fail() {
				//console.log(456)
			}
		})
		this.avatar=uni.getStorageSync('headpic')
	},
	onShow() {
		
		this.avatar=uni.getStorageSync('headpic')
		
	},
	onHide() {
		this.avatar=uni.getStorageSync('headpic')
	},
	methods:{
		bossArea:function(e){
			this.city='';
			this.city=e.detail.value
			//console.log(this.city)
		},
		changHeadpic:function(){
			uni.navigateTo({
				url:'/pages/upHead/index?status='+0
			})
		},
		upLicense:function(){
			var that = this;
			// var childId = wx.getStorageSync("child_id");
			// var token = wx.getStorageSync('token');
			wx.chooseImage({
			count: 1, // 最多可以选择的图片张数，默认9
			sizeType: ['compressed'], // original 原图，compressed 压缩图，默认二者都有
			sourceType: ['album', 'camera'], // album 从相册选图，camera 使用相机，默认二者都有
			success: function (res) {
			let avatar = res.tempFilePaths;
			that.License=avatar;
			that.uplicense=true;
			},
			fail: function () {
			// fail
			},
			complete: function () {
			// complete
			}
			})
		},
		submitData:function(e){
			let _this=this;
			
			let openid=uni.getStorageSync('openid');
			//console.log(openid)
			
			if(e.detail.value.address!='' && e.detail.value.address!=undefined){
				e.detail.value.address=this.city[0]+this.city[1]+this.city[2]+e.detail.value.address;
			}else{
				e.detail.value.address=this.city[0]+this.city[1]+this.city[2];
			}
			
			e.detail.value.openId=openid;
			e.detail.value.roleType=2;
			
			//console.log(_this.License[0])
			//console.log(e.detail.value);
			if(e.detail.value.userName==''||e.detail.value.jointName==''||e.detail.value.address==''||this.uplicense==false){
				uni.showModal({
					title:'提示',
					content:'请输入完整信息'
				})
				return false
			}
			// else if (!(/^[1](([3|5|8][\\d])|([4][4,5,6,7,8,9])|([6][2,5,6,7])|([7][^9])|([9][1,8,9]))[\\d]{8}$/.test(e.detail.value.phone))){
			// 	uni.showModal({
			// 		title: '提示',
			// 		content: '请填写正确的手机号'
			// 		})
			// }
			
			
			uni.showLoading();
			
			uni.uploadFile({
			    url: _this.$store.state.baseurl+'/user/fileUpload',
				filePath: _this.avatar,
				name: 'headUrl',
			    formData: {
					openId:openid
				},
			    method: 'POST',
			    header: {
			        "Content-Type": "multipart/form-data"
			    },
			    success: (res) => {
			        
			    },
				fail() {
					uni.hideLoading()
				}
			});
			
			 uni.uploadFile({
			    url: _this.$store.state.baseurl+'/user/insertUser',
				filePath: _this.License[0],
				name: 'file',
			    formData: e.detail.value,
			    method: 'POST',
			    header: {
			        "Content-Type": "multipart/form-data"
			    },
			    success: (res) => {
			        //openId、或SessionKdy存储//隐藏loading
			        uni.hideLoading();
					// uni.navigateTo({//信息更新成功后跳转到小程序首页
					//     url: '/pages/identity/index'
					// });
					uni.reLaunch({
						url:'/pages/status/index'
					})
			    },
				fail() {
					uni.hideLoading()
				}
			});
		}
	}
}
